/*
 * Sidebar
 */

.sidebar {
  position: fixed;
  bottom: 0;
  left: 0;
  top: 5rem;
  padding: 0;
  z-index: 100;

  svg {
    visibility: hidden;
  }

  ul > li > a {
    display: block;
    padding: 0.5rem 1.5rem;
    color: #3c4043;
    text-decoration: none;
    &.active {
      background-color: var(--light);
      color: var(--primary);
    }
    &:not(.active):hover {
      background-color: rgba(0, 0, 0, 0.039);
      svg{
        visibility: visible;
      }
    }
  }
}

.sidebar-sticky {
  position: -webkit-sticky;
  position: sticky;
  /* Height of navbar */
  height: 100vh;
  overflow-x: hidden;
  overflow-y: auto;
  /* Scrollable contents if viewport is shorter than content. */
}
