$dt-placeholder-color: rgba(0, 0, 0, 0.56);

select {
  cursor: pointer;
}

.payment .nav-item:hover,
.payment .nav-item.selected {
  background-color: var(--light);
}

svg {
  cursor: pointer;
  width: 1.25rem;
  height: 1.25rem;
}

table svg {
  width: 1.25rem;
  height: 1.25rem;
}

input::placeholder {
  color: $dt-placeholder-color;
}

@import "scss/Header";
@import "scss/Sidebar";
@import "scss/Action";
